.switch {
  position: relative;
  display: inline-block;
  width: 90px;
  height: 45px;
  background-color: #7a7878;
  color: #000;
  transition: all 0.6s;
  background-color: #13fd46;
}

.switch.off {
  border-radius: 30px;
  padding: 5px;
  cursor: pointer;
  background-color: #fd1313;
}
.switch.on {
  border-radius: 30px;
  padding: 5px;
  cursor: pointer;
}

.switch.off:before,
.switch.off:after {
  transition: all 0.6s;
}

.switch.on:before,
.switch.on:after {
  transition: all 0.6s;
}

.switch.off:before,
.switch.on:before {
  display: block;
  position: absolute;
  content: "";
  height: 40px;
  width: 40px;
  left: 2px;
  top: 2px;
  background-color: #ffffff;
  transition: all 0.6s;
  transition-delay: 0.01s;
  border-radius: 50%;
}

.switch.off:after {
  color: #fd1313;
  display: block;
  position: absolute;
  content: "OFF";
  /* -o-transform: rotate(-270deg);
    -webkit-transform: rotate(-270deg);
    -ms-transform: rotate(-270deg);
    transform: rotate(-270deg); */
  left: 10px;
  top: 10px;
}

.switch.on:before {
  -webkit-transform: translateX(45px);
  transform: translateX(45px);
}

.switch.on:after {
  color: #13fd46;
  position: absolute;
  content: "NO";
  /* -o-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg); */
  left: 58px;
  top: 10px;
}

